@import url(https://fonts.googleapis.com/css?family=Raleway:400,300,500,700);
@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-text {
    font-family: 'Raleway', sans-serif;
    font-weight: 300;
    font-size: 3em;
    color: #ECE5CE;
    padding-top: 10%;
}
.button_with_text {
    position: relative;
    padding-top: 15%;
}

.button_visible {
    background-color: #ECE5CE;
    color: #1F3944;
    width: 50%;
    border: 0px;
    height: 50px;
    border-radius: 5px;
    font-size: 1.2em;
}

.button_invisible {
    visibility: hidden;
}
  
.slider {
    position:absolute;
    top:0;
    left:-100%;
    width:70%;
    overflow:hidden;
    -webkit-animation: fadein 3s ease-in;
            animation: fadein 3s ease-in;
}
 
.slider-active {
    left:15%;
}

.slider_input_field {
    background: transparent;
    border: 0px;
    border-bottom: 1px solid #ECE5CE;
    height: 50px;
    color: #ECE5CE;
    font-size: 1.2em;
    font-family: 'Raleway' sans-serif;
    font-weight: 300;
}

@-webkit-keyframes fadein {
    from { 
        opacity: 0; 
    }
    to { 
        opacity: 1; 
    }
}

@keyframes fadein {
    from { 
        opacity: 0; 
    }
    to { 
        opacity: 1; 
    }
}
.loader_button_container {
  background-color: #83AF9B;
  color: #333;
  font-family: 'Raleway', sans-serif;
  text-transform: uppercase; }

input[type="checkbox"] {
  display: none; }
  input[type="checkbox"]:checked + label {
    -webkit-animation: loading-background 4s linear forwards;
            animation: loading-background 4s linear forwards; }
  input[type="checkbox"]:checked + label::after {
    -webkit-animation: loading 4s linear forwards;
            animation: loading 4s linear forwards; }

label {
  background-color: #FE4365;
  cursor: pointer;
  display: block;
  height: 0;
  left: 50%;
  margin-left: -5em;
  margin-top: -1.5em;
  position: absolute;
  text-align: center;
  top: 50%;
  width: 10em; }
  label::before {
    background-color: #ECE5CE;
    content: "";
    display: block;
    height: 2.6em;
    margin: 0.2em;
    outline: 0.4em solid #ECE5CE;
    outline-offset: 0.2em;
    position: absolute;
    width: 9.6em; }
  label::after {
    content: "Continue";
    line-height: 3em;
    position: relative; }

@-webkit-keyframes loading-background {
  from {
    height: 0; }
  to {
    height: 3em; } }

@keyframes loading-background {
  from {
    height: 0; }
  to {
    height: 3em; } }

@-webkit-keyframes loading {
  0% {
    content: "Loading"; }
  30% {
    content: "Loading."; }
  60% {
    content: "Loading.."; }
  90% {
    content: "Loading..."; }
  100% {
    content: "Loading...."; } }

@keyframes loading {
  0% {
    content: "Loading"; }
  30% {
    content: "Loading."; }
  60% {
    content: "Loading.."; }
  90% {
    content: "Loading..."; }
  100% {
    content: "Loading...."; } }

.start_page_container {
    padding-top: 10%;
}
.hit_button {
    font-family: "Raleway" sans-serif;
    position: absolute;
    font-size: 1.5em;
    text-transform: uppercase;
    padding: 7px 20px;
    left: 50%;
    width: 200px;
    height: 200px;
    margin-left: -100px;
    color: #1F3944;
    border-radius: 50%;
    border: 5px solid transparent;
    background: #ECE5CE;
    cursor: pointer;
    outline: 0 !important;
    -webkit-animation: pulse 1s infinite alternate;
            animation: pulse 1s infinite alternate;
    transition: background 0.4s, border 0.2s, margin 0.2s;
}

.hit_button:hover {
    background: #ECE5CE;
    margin-top: -2px;
    -webkit-animation: none;
            animation: none;
}

.hit_button:active {
    margin-top: 5px;
    background: #1F3944;
    color: #ECE5CE;
}

@-webkit-keyframes pulse {
    0% {
        margin-top: 0px;
    }
    100% {
        margin-top: 6px; 
    } 
}

@keyframes pulse {
    0% {
        margin-top: 0px;
    }
    100% {
        margin-top: 6px; 
    } 
}
.game_page_container {
    padding-top: 15%;
}

.current_score {
    font-family: "Raleway" sans-serif;
    margin-top: 250px;
    font-size: 2em;
    color: #ECE5CE;
    font-weight: 300;
}

.timer {
    font-family: "Raleway" sans-serif;
