    font-weight: 300;
}
.current_score_container {
    font-family: "Raleway" sans-serif;
    font-size: 2em;
    color: #ECE5CE;
    font-weight: 300;
    margin-top: 10px;
    margin-bottom: 10px;
}

.custom_table_style{
    width: 90%;
    margin-left: auto;
